import React, { useEffect, useState } from 'react';
import M from 'materialize-css';

import RangeSelector from '../../components/RangeSelector'

const Search = () => {
    let buildingSelectorRef;
    let facingSelectorRef;

    const [amountRange, setAmountRange] = useState([3000000, 7000000]);
    const [cabinRange, setCabinRange] = useState([2, 14]);
    const [conferenceRange, setConferenceRange] = useState([5, 10]);
    const [workstationRange, setWorkstationRange] = useState([3, 8]);

    useEffect(() => {
        M.FormSelect.init(buildingSelectorRef);
        M.FormSelect.init(facingSelectorRef);
    }, [buildingSelectorRef, facingSelectorRef]);

    const renderBuildingSelector = () => (
        <div class="col s12 m6">
            <select ref={select => { buildingSelectorRef = select; }}>
                <option value="0">Select building name</option>
                <option value="1">Aditya Heights</option>
            </select>
        </div>
    );

    const renderFacingSelector = () => (
        <div class="col s12 m6">
            <select ref={select => { facingSelectorRef = select; }} >
                <option value="0">Select facing</option>
                <option value="1">North-East</option>
            </select>
        </div>

    );

    const renderFloorRangeSelector = () => {
        return (
            <div className="col s12 m6">
                renderFloorRangeSelector
            </div>
        )
    }

    const renderAmountRangeSelector = () => {
        return (
            <RangeSelector
                label="Amount"
                valuePrefix="₹"
                value={amountRange}
                onChange={setAmountRange}
                min={0}
                max={9999999}
                step={100000}
                containerClass={"col s12 m6"}
            />
        )
    }

    const renderCabinRangeSelector = () => {
        return (
            <RangeSelector
                small
                label="Cabin"
                value={cabinRange}
                onChange={setCabinRange}
                min={0}
                max={15}
                step={1}
                containerClass={"col s12 m6"}
            />
        )
    }

    const renderConferenceRangeSelector = () => {
        return (
            <RangeSelector
                medium
                label="Conference"
                value={conferenceRange}
                onChange={setConferenceRange}
                min={0}
                max={15}
                step={1}
                containerClass={"col s12 m6"}
            />
        )
    }

    const renderWorkstationRangeSelector = () => {
        return (
            <RangeSelector
                medium
                label="Work Station"
                value={workstationRange}
                onChange={setWorkstationRange}
                min={0}
                max={15}
                step={1}
                containerClass={"col s12 m6"}
            />
        )
    }

    const renderSuperAreaRangeSelector = () => {
        return (
            <div className="col s12 m6">
                renderSuperAreaRangeSelector
            </div>
        )
    }

    const renderBuiltUpAreaRangeSelector = () => {
        return (
            <div className="col s12 m6">
                renderBuiltUpAreaRangeSelector
            </div>
        )
    }

    const renderAmenities = () => (
        <div className="row">
            <div className="col s6 m3">
                <label>
                    <input type="checkbox" />
                    <span>A/C</span>
                </label>
            </div>
            <div className="col s6 m3">
                <label>
                    <input type="checkbox" />
                    <span>Pantry</span>
                </label>
            </div>
            <div className="col s6 m3">
                <label>
                    <input type="checkbox" />
                    <span>Washroom</span>
                </label>
            </div>
            <div className="col s6 m3">
                <label>
                    <input type="checkbox" />
                    <span>Freehold</span>
                </label>
            </div>
        </div>
    );
    return (
        <div class="col s12">
            <div class="card">
                <div class="card-content">
                    <div className="row">
                        {renderBuildingSelector()}
                        {renderFacingSelector()}
                        {/* Dropdown Floor */}
                        {/* {renderFloorRangeSelector()} */}

                        {renderCabinRangeSelector()}
                        {renderConferenceRangeSelector()}

                        {/* Range Workstation */}
                        {renderWorkstationRangeSelector()}

                        {/* Range Super area */}
                        {/* {renderSuperAreaRangeSelector()} */}

                        {/* Range Cover area */}
                        {/* {renderBuiltUpAreaRangeSelector()} */}

                        {/* Range Amount */}
                        {renderAmountRangeSelector()}

                    </div>
                    {renderAmenities()}
                </div>
                <div class="card-action">
                    {/* <button className="btn waves-effect black">Clear Filter</button>
                    <button className="btn waves-effect black">Apply Filter</button>
                     */}
                    <a className="waves-effect btn white black-text">Clear</a>
                    <a className="waves-effect btn black right">Apply</a>
                </div>
            </div>
        </div>
    )
}

export default Search;